.grid-container {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between; */
    width: 100%;
}

.grid-container > * {
    flex: 0 1 calc(20% - 1rem);
    margin: 1rem .5rem;
}

@media (max-width: 1300px) {
    .grid-container > * {
        flex: 0 1 calc(33% - 1rem);
    }
}

@media (max-width: 768px) {
    .grid-container > * {
        flex: 0 1 100%;
    }
}